___CSS_LOADER_EXPORT___.push([module.id, `/* Reset and base styles  */
* {
    padding: 0px;
    margin: 0px;
    border: none;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Links */

a,
a:link,
a:visited {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

/* Common */

aside,
nav,
footer,
header,
section,
main {
    display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    font-size: inherit;
    font-weight: inherit;
}

ul,
ul li {
    list-style: none;
}

img {
    vertical-align: top;
}

img,
svg {
    max-width: 100%;
    height: auto;
}

address {
    font-style: normal;
}

/* Form */

input,
textarea,
button,
select {
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    background-color: transparent;
}

input::-ms-clear {
    display: none;
}

button,
input[type="submit"] {
    display: inline-block;
    box-shadow: none;
    background-color: transparent;
    background: none;
    cursor: pointer;
}

input:focus,
input:active,
button:focus,
button:active {
    outline: none;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

label {
    cursor: pointer;
}

legend {
    display: block;
}`, "",{"version":3,"sources":["webpack://./styles/reset.css"],"names":[],"mappings":"AAAA,2BAA2B;AAC3B;IACI,YAAY;IACZ,WAAW;IACX,YAAY;AAChB;;AAEA;;;IAGI,sBAAsB;AAC1B;;AAEA,UAAU;;AAEV;;;IAGI,qBAAqB;AACzB;;AAEA;IACI,qBAAqB;AACzB;;AAEA,WAAW;;AAEX;;;;;;IAMI,cAAc;AAClB;;AAEA;;;;;;;IAOI,kBAAkB;IAClB,oBAAoB;AACxB;;AAEA;;IAEI,gBAAgB;AACpB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;;IAEI,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,kBAAkB;AACtB;;AAEA,SAAS;;AAET;;;;IAII,oBAAoB;IACpB,kBAAkB;IAClB,cAAc;IACd,6BAA6B;AACjC;;AAEA;IACI,aAAa;AACjB;;AAEA;;IAEI,qBAAqB;IACrB,gBAAgB;IAChB,6BAA6B;IAC7B,gBAAgB;IAChB,eAAe;AACnB;;AAEA;;;;IAII,aAAa;AACjB;;AAEA;IACI,UAAU;IACV,SAAS;AACb;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,cAAc;AAClB","sourcesContent":["/* Reset and base styles  */\r\n* {\r\n    padding: 0px;\r\n    margin: 0px;\r\n    border: none;\r\n}\r\n\r\n*,\r\n*::before,\r\n*::after {\r\n    box-sizing: border-box;\r\n}\r\n\r\n/* Links */\r\n\r\na,\r\na:link,\r\na:visited {\r\n    text-decoration: none;\r\n}\r\n\r\na:hover {\r\n    text-decoration: none;\r\n}\r\n\r\n/* Common */\r\n\r\naside,\r\nnav,\r\nfooter,\r\nheader,\r\nsection,\r\nmain {\r\n    display: block;\r\n}\r\n\r\nh1,\r\nh2,\r\nh3,\r\nh4,\r\nh5,\r\nh6,\r\np {\r\n    font-size: inherit;\r\n    font-weight: inherit;\r\n}\r\n\r\nul,\r\nul li {\r\n    list-style: none;\r\n}\r\n\r\nimg {\r\n    vertical-align: top;\r\n}\r\n\r\nimg,\r\nsvg {\r\n    max-width: 100%;\r\n    height: auto;\r\n}\r\n\r\naddress {\r\n    font-style: normal;\r\n}\r\n\r\n/* Form */\r\n\r\ninput,\r\ntextarea,\r\nbutton,\r\nselect {\r\n    font-family: inherit;\r\n    font-size: inherit;\r\n    color: inherit;\r\n    background-color: transparent;\r\n}\r\n\r\ninput::-ms-clear {\r\n    display: none;\r\n}\r\n\r\nbutton,\r\ninput[type=\"submit\"] {\r\n    display: inline-block;\r\n    box-shadow: none;\r\n    background-color: transparent;\r\n    background: none;\r\n    cursor: pointer;\r\n}\r\n\r\ninput:focus,\r\ninput:active,\r\nbutton:focus,\r\nbutton:active {\r\n    outline: none;\r\